<p><br></p>
<p><span class="rvts17">民事能力</span></p>
<p><img width="595" height="758" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber142.jpg"><span class="rvts18"></span>
<br><span class="rvts21">民事能力可以影响角色在游戏中的物品买卖价格，对话中的说服力或恐吓，对整体队伍成员的加成影响和找到更好装备的几率。当角色升级到</span><span class="rvts20">1,2,6,10,14,18,22,26,30,34</span><span class="rvts21">级时，会得到该技能系统的额外</span><span class="rvts20">1</span><span class="rvts21">点分配值。</span>
<br><span class="rvts21">玩家不能在同一民事能力上加点超过</span><span class="rvts20">5</span><span class="rvts21">点。但是某些装备会提供额外的能力点数加成。值得注意的是，</span><span class="rvts20"></span><span class="rvts21">孤狼</span><span class="rvts20"></span><span class="rvts21">天赋对民事能力点数上限并无加成效果。</span>
<br><span class="rvts18"><br></span></p>
