<td width="56" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">博学者</span></p>
</td>
<td width="85" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Loremaster</span></p>
</td>
<td width="460" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">该技能可以提升角色更快更多的鉴定出敌人和宝物</span></p>
</td>
<td width="640" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">更高级别的宝物需要更高级别的博学者属性值才可以被鉴定出来</span></p>
</td>
</tr>
<tr valign="top">
<td width="122" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber147.jpg"></p>
</td>
<td width="56" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">隔空取物</span></p>
</td>
<td width="85" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Telekinesis</span></p>
</td>
<td width="460" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">该技能可以提高角色搬起物品的距离长短</span></p>
</td>
<td width="640" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">每一等级提供额外</span><span class="rvts20">6</span><span class="rvts21">米的搬运距离</span></p>
</td>
</tr>
<tr valign="top">
<td width="122" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber148.jpg"></p>
</td>
<td width="56" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">潜行</span></p>
</td>
<td width="85" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Sneaking</span></p>
</td>
<td width="460" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">该技能决定角色在潜行时被抓的几率</span></p>
</td>
<td width="640" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">该技能更高等级会使角色进行潜行时，敌人的觉察视野缩小，并使角色的潜行速度增加</span></p>
</td>
</tr>
<tr valign="top">
<td width="122" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber149.jpg"></p>
</td>
<td width="56" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
